<script>
import axios from 'axios';
import NftRentalHistory from '../components/pages/nft-rental/NftRentalHistory.vue';

export default {
  name: 'NftViewPage',
  components: {
    NftRentalHistory
  },
  
  data() {
    return {
      nft: null,
      loading: true,
      error: null,
      copied: null,
      metadata: null,
      metadataLoading: false,
      metadataError: null
    };
  },
  
  created() {
    this.fetchNftData();
  },
  
  methods: {
    async fetchNftData() {
      this.loading = true;
      this.error = null;
      
      try {
        const assetPublicKey = this.$route.params.assetPublicKey;
        const response = await axios.get(`/nft-bitcat/nft/${assetPublicKey}`);
        
        if (response.data.success) {
          this.nft = response.data.nft;
          if (this.nft.metadataUri) {
            this.fetchMetadata(this.nft.metadataUri);
          }
        } else {
          this.error = response.data.message || 'Failed to load NFT data';
        }
      } catch (error) {
        console.error('Error fetching NFT data:', error);
        this.error = error.response?.data?.message || 'An error occurred while fetching NFT data';
      } finally {
        this.loading = false;
      }
    },
    
    async fetchMetadata(metadataUri) {
      this.metadataLoading = true;
      this.metadataError = null;
      
      try {
        let response;
        
        try {
          // First attempt: direct request to the metadata URI
          response = await axios.get(metadataUri, {
            headers: {
              'Accept': 'application/json'
            }
          });
        } catch (directError) {
          console.warn('Direct metadata fetch failed, trying CORS proxy:', directError);
          
          // Second attempt: use a CORS proxy
          const corsProxyUrl = `https://cors-anywhere.herokuapp.com/${metadataUri}`;
          response = await axios.get(corsProxyUrl, {
            headers: {
              'Accept': 'application/json',
              'X-Requested-With': 'XMLHttpRequest'
            }
          });
        }
        
        this.metadata = response.data;
        console.log('Metadata loaded:', this.metadata);
      } catch (error) {
        console.error('Error fetching metadata:', error);
        this.metadataError = 'Failed to load metadata information. CORS policy might be blocking the request.';
      } finally {
        this.metadataLoading = false;
      }
    },
    
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    
    copyToClipboard(text, elementId) {
      navigator.clipboard.writeText(text).then(() => {
        this.copied = elementId;
        this.$toast.success('Copied to clipboard!', {
          position: 'top-right',
          duration: 2000
        });
        setTimeout(() => {
          this.copied = null;
        }, 2000);
      }).catch(err => {
        console.error('Failed to copy text: ', err);
        this.$toast.error('Failed to copy to clipboard', {
          position: 'top-right',
          duration: 2000
        });
      });
    }
  }
};
</script>

