<template>
  <div class="nft-rental-history">
    <h3 class="section-title">Rental History</h3>
    
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading rental history...</p>
    </div>
    
    <div v-else-if="rentalHistory.length === 0" class="empty-state">
      <p>This NFT has not been rented yet.</p>
    </div>
    
    <div v-else class="rental-history-grid">
      <div v-for="rental in rentalHistory" :key="rental.id" class="rental-card">
        <div class="rental-card-header">
          <div class="rental-price">
            <i class="fas fa-coins"></i>
            {{ formatPrice(rental.price) }} SOL
          </div>
          <button 
            v-if="rental.transaction" 
            @click="viewTransaction(rental.transaction)"
            class="view-tx-button"
          >
            <i class="fas fa-receipt"></i> TX
          </button>
        </div>
        
        <div class="rental-card-body">
          <div class="rental-detail-row">
            <span class="detail-label"><i class="fas fa-user"></i> Rented By:</span>
            <router-link :to="`/player/${rental.rentedBy}`" class="user-link">
              {{ rental.rentedBy }}
            </router-link>
          </div>
          
          <div class="rental-detail-row">
            <span class="detail-label"><i class="fas fa-calendar-day"></i> {{ formatDate(rental.startDate) }} for {{ rental.days }} day(s)</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';

export default {
  name: 'NftRentalHistory',
  props: {
    mint: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const store = useStore();
    const loading = ref(true);
    const rentalHistory = ref([]);
    
    const fetchRentalHistory = async () => {
      try {
        loading.value = true;
        const response = await axios.get(`/nft-rental/nft/${props.mint}/history`);
        rentalHistory.value = response.data.history || [];
      } catch (error) {
        console.error('Error fetching NFT rental history:', error);
        // store.dispatch('notify', {
        //   type: 'error',
        //   message: 'Failed to load NFT rental history'
        // });
      } finally {
        loading.value = false;
      }
    };
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatDate = (dateString) => {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    const viewTransaction = (signature) => {
      const explorerUrl = `https://explorer.solana.com/tx/${signature}`;
      window.open(explorerUrl, '_blank');
    };
    
    onMounted(() => {
      if (props.mint) {
        fetchRentalHistory();
      }
    });
    
    return {
      loading,
      rentalHistory,
      formatPrice,
      formatDate,
      viewTransaction
    };
  }
};
</script>

